@import '../../variables.scss';

.tasklist__header {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  background-color: $header-color;
  align-items: center;
  color: #fff;
  .tasklist__personal {
    display: flex;
    align-items: center;
    .personal__name {
      margin-left: 10px;
    }
  }
  .logout__btn {
    height: 32px;
    width: 32px;
    border: none;
    background-color: $logout-color;
    border-radius: 4px;
    opacity: .9;
    color: #fff;
    transition: background-color ease .4s;
    &:hover {
      background-color: #42526e;
    }
  }
}

.tasklist__background {
  width: 100%;
  .tasklist__content {
    padding-top: 60px;
    .add__btn {
      background-color: hsla(0,0%,100%,.26);
      border: none;
      border-radius: 4px;
      padding: 6px 8px;
      width: 272px;
      display: flex;
      align-items: center;
      margin-top: 2px;
      transition: background-color ease .4s;
      &::before {
        content: '+';
        font-size: 20px;
        margin-right: 10px;
      }
      &:hover {
        background-color: hsla(0,0%,100%,.46);
      }
    }
    .add__btn--task {
      border: none;
      background: transparent;
      width: 100%;
      &:hover {
        background-color: rgba(9,30,66,.08);
      }
    }
  }
}
